.checkbox > span::after {
  content: "";
  width: 20px;
  height: 20px;
  background: #2b2a29;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
}

.checkbox > span::before {
  content: "";
  width: 20px;
  height: 20px;
  background: url(../images/svg/check.svg) no-repeat center center;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  z-index: 2;
}

.checkbox input:checked + span::after {
  background: #00b2e3;
}

.checkbox input:checked + span::before {
  opacity: 1;
}

.radio {
  padding-left: 30px;
