}

.radio > span::after {
  content: "";
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 50%;
  position: absolute;
  border: 1px solid rgba(0, 0, 0, 0.2);
  left: 0;
  top: 0;
}

.radio > span::before {
  content: "";
  width: 10px;
  height: 10px;
  background: #00b2e3;
  border-radius: 50%;
  position: absolute;
  left: 5px;
  top: 5px;
