/*===== global styles =====*/
button,
input {
  padding: 0;
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 0;
  appearance: none;
  outline: none;
  color: inherit;
  background-color: transparent;
}

button {
  cursor: pointer;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

html,
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 360px;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  line-height: 1;
  font-size: 100%;
  -webkit-text-size-adjust: 100%;
  background-color: #fff;
}
body.lock {
  overflow: hidden;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  outline: none;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
  image-rendering: -webkit-optimize-contrast;
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.15s ease;
}

input,
button,
textarea,
select {
  font: inherit;
  line-height: inherit;
}

input[type=text]::-ms-clear {
  display: none;
  width: 0;
  height: 0;
}

input[type=text]::-ms-reveal {
  display: none;
  width: 0;
  height: 0;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  display: none;
